    -ms-flex: 1 1;
        flex: 1 1;
    margin-left: 0.2em;
}

.TimeRow .deleteButton {
    background: transparent;
    border: none;
    color: #B00020;
    cursor: pointer;
    -ms-flex: 0 0;
        flex: 0 0;
    font-size: 14px;
